

.global {
    width: 100%;
    position: relative;
    margin-top: 10vh;
    background: var(--third-blue);
    color: var(--primary-white);
    padding: 50px;
}

.title {
    width: 100%;
    font-size: 4em;
    font-weight: 500;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 20px;
}

.content {
    font-weight: 500;
    line-height: 1.5;
    font-size: 1.4rem;
    word-spacing: 3px;
    padding-left: 2vw;
    position: relative;
    text-align: center;
    color: var(--primary-white);
    padding-top: 10px;
    padding-bottom: 10px;
    margin: 20px;
    width: 90%;

}